.container {
    max-width: 700px;
    margin: 20px auto;
    padding: 30px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #ffffff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    font-family: 'Roboto', sans-serif;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.group-container {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%; 
}


.default-label {
    width: 80px;
    margin-right: 15px; /* Add space between the label and radio buttons */
    font-weight: bold;
    font-size: 14px;
    text-align: left; 
}

.smaller-label {
    width: max(10%, 100px);
    margin-right: 15px; /* Add space between the label and radio buttons */
    font-size: 12px;
    text-align: left; 
}

.input-field {
    flex-grow: 1;
    width: 100%; 
}

.submit-button {
    padding: 12px;
    font-size: 18px;
    background-color: #007BFF;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.submit-button:hover {
    background-color: #0056b3;
}

.radio-group {
    display: flex;
    gap: 10px; /* Add spacing between radio buttons */
    font-size: 10px; 
}

.mode-options {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 16px; 
    margin-top: 0px;
    background-color: #f9f9f9
}

.checkbox-group {
    display: flex;
    gap: 15px; /* Space between checkboxes */
    align-items: center;
}

.dropdown-container {
    display: flex;
    align-items: center;
    gap: 10px; /* Space between label and dropdown */
}

.dropdown-label {
    font-size: 14px;
    font-weight: bold;
}

.collapse-toggle {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
    font-weight: bold;
    color: #007bff;
    cursor: pointer;
    margin: 10px 0;
    transition: color 0.3s ease;
}

.collapse-toggle:hover {
    color: #0056b3;
}

.collapse-text {
    font-size: 14px;
    cursor: pointer;
}

.message {
    text-align: center;
    font-size: 16px;
    color: #007BFF;
    margin-top: 15px;
}