.container {
    max-width: 100%; /* Ensure it fits inside the flexbox */
    margin: 0;
    padding: 0;
    border: none; /* Remove redundant border */
    box-shadow: none; /* Let App.css handle shadows */
}
h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

th, td {
    padding: 10px;
    text-align: center;
    border: 1px solid #ddd;
}

th {
    background-color: #f2f2f2;
    font-weight: bold;
}

.footer {
    margin-top: 20px;
    font-style: italic;
    text-align: right;
}